import Vue from 'vue';
import Vuex from 'vuex';
import {stores as ModuleStores} from './views';

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    endpoint: 'https://api.silverspring65.ru/api',
    snackbar: {
      show: false,
      duration: 4000,
      message: ''
    },
    title: '',
    backButton: true,
    backRoute: {name: 'Home'},
    progress: false,
    toolbar: false,
    popup: false
  },
  mutations: {
    popup(state, val) {
        state.popup = (val === undefined) ? !(state.popup) : val;
    },
    toggleToolbar(state, val) {
      state.toolbar = (val === undefined) ? !(state.toolbar) : val;
    },
    setTitle(state, title) {
      state.title = title;
    },
    setBackRoute(state, route) {
      state.backRoute = Object.assign({}, route);
    },
    showBackButton(state) {
      state.backButton = true;
    },
    hideBackButton(state) {
      state.backButton = false;
    },
    showProgressBar(state) {
      state.progress = true;
    },
    hideProgressBar(state) {
      state.progress = false;
    },
    toggleProgressBar(state) {
      state.progress = !(state.progress);
    },
    showSnackbar(state, data) {
      let timeOut = (data[1]) ? data[1] : 4000;
      state.snackbar.duration = timeOut;
      state.snackbar.message  = data[0];
      state.snackbar.show     = true;
      setTimeout(() => {
        state.snackbar.show = false;
        state.snackbar.message = '';
      }, timeOut);
    },
    hideSnackbar(state) {
      state.snackbar.show    = false;
      state.snackbar.message = '';
    },
  },
  actions: {
    init({ dispatch }) {
      dispatch('address/getDistricts');
      dispatch('address/getSchedules');
      dispatch('address/getNotDefaultSchedules');
    },
  },
  modules: ModuleStores
});



