import btn from './../btn/index.vue';
import iconBtn from './../icon-btn/index.vue';
import inputText from './../input-text/index.vue';
import Datepicker from 'vuejs-datepicker';
import {ru} from 'vuejs-datepicker/dist/locale';
export default {
  name: 'date-selector',
  components: {btn, iconBtn, inputText, Datepicker},
  props: {
    disabledDates:{type: Object, default: () => {}},
    highlighted:{type: Object, default: () => {}},
    label:  {type: String, default: ''},
    name: {type: String, default: ''},
    min: {type: Number, default: 0},
    max: {type: Number, default: 999},
    error: {type: String, default: ''},
    size: {type: String, default: 'normal'},
    value: {
      type: Date,
      default: () => {
        return new Date();
      }
    },
  },
  data() {
    return {
      inputValue: 0,
      hasTextValue: false,
      locale: ru
    }
  },
  watch: {
    '$props.value'(newVal) {
      this.inputValue = newVal;
    },
    'inputValue'(newVal) {
      this.$emit('input', newVal);
      this.$emit('change', newVal);
      setTimeout(() => this.changeTextValue(newVal), 0);
    }
  },
  computed: {
    inputId() {
      return 'input-' + this._uid;
    },
    /**
     * Может ли пользователь выбрать следующий день
     * @returns {boolean}
     */
    canNextDay() {
        if (!this.$props.disabledDates || !this.$props.disabledDates.from) return true;
        let from = this.moment(this.$props.disabledDates.from, 'DD.MM.YYYY'),
            date = this.moment(this.$props.value, 'DD.MM.YYYY').add(1, 'days');
        return from.isAfter(date);
    },
    /**
     * Может ли пользователь выбрать предыдущий день
     * @returns {boolean}
     */
    canPrevDay() {
        if (!this.$props.disabledDates || !this.$props.disabledDates.to) return true;
        let to = this.moment(this.$props.disabledDates.to, 'DD.MM.YYYY'), date = this.moment(this.$props.value, 'DD.MM.YYYY').subtract(1, 'days');
        return to.isBefore(date);
    },
    sizeVal() {
        return (this.$props.size && (['small', 'normal', 'big', 'extra-big'].indexOf(this.$props.size)+1)) ? this.$props.size : 'normal'
    }
  },
  methods: {
    onSelected(date) {
      this.$emit('input', date);
      this.$emit('change', date);
    },
    onFocus() {
      this.$emit('focus');
    },
    onBlur() {
      this.$emit('blur');
    },
    /**
     * Проверяет является ли выбранная дата сегоня или завтра,
     * если да, то вместо цифр в инпуте показывал надписи "сегодня" или "завтра"
     * @param value
     */
    changeTextValue(value) {
      if (!value || !this.$refs.datepicker || !this.$refs.datepicker.$el) return;
      let inputGroup = this.$refs.datepicker.$el.querySelector('.input-group');
      let result = false;

      let current  = this.moment(value).format('DD.MM.YYYY'),
          today    = this.moment(new Date()).format('DD.MM.YYYY'),
          tomorrow = this.moment(new Date()).add(1, 'days').format('DD.MM.YYYY');

      if (current === today) result = 'Сегодня';
      else if (current === tomorrow) result = 'Завтра';
      else result = this.moment(value).format('Do MMMM (ddd)');

      let closeCalendarFunc = function (e) {
          if (
              !e.target.closest('.vdp-datepicker') &&
              !e.target.classList.contains('.vdp-datepicker') &&
              this.$refs.datepicker
          ) this.$refs.datepicker.close();
      }.bind(this);

        let textValue = document.createElement('div');
        textValue.classList.add('text-value');
        textValue.innerHTML = '<b>' + this.$props.label + '</b><span>' + result.toString() + '</span>';

        inputGroup.appendChild(textValue);
        textValue.addEventListener('click', () => this.$refs.datepicker.showCalendar());
        document.addEventListener('click', closeCalendarFunc);
        this.hasTextValue = true;

      if (result) {

      } else {
        //document.removeEventListener('click', closeCalendarFunc);
        //let textValue = inputGroup.querySelectorAll('.text-value');
        //if (textValue) textValue.forEach(item => item.remove());
        //this.hasTextValue = false;
      }
    },
    /**
     * Находит сколько дней нужно прибавить к текущей дате
     * чтобы получить следующий рабочий день
     * @returns {Object}
     */
    getNextDay() {
      let defaultDay = {date: this.moment(this.$props.value, 'DD.MM.YYYY').add(1, 'days'), addDays: 1};
        if (!this.$props.disabledDates || !this.$props.disabledDates.from) return defaultDay;
        let result = false;
        if (this.$props.disabledDates.dates) {
          let lastDay = this.moment(this.$props.disabledDates.from, 'DD.MM.YYYY');
          //составим массив выходных, для простого поиска
          let weekends = this.$props.disabledDates.dates.map(item => this.moment(item).format('DD.MM.YYYY')), nextDay;

          //идем на 2 недели вперёд и ищем ближайшую дату, когда работает компания
          for (let i = 1; i <= 14; i++) {
              if (result) return result; //если дата уже найдена, то вернем результат
              nextDay = this.moment(this.$props.value, 'DD.MM.YYYY').add(i, 'days');
              //если следующий день ДО максимальной даты и компания в этот день работает
              if (nextDay.isBefore(lastDay) && !(weekends.indexOf(nextDay.format('DD.MM.YYYY'))+1))
                  result = {date: nextDay, addDays: i};
          }
        }

        //если в итоге ничего не нашли, вернем стандарт - 1 день
        return defaultDay;
    },
    /**
     * Находит сколько дней нужно удалить у текущей даты
     * чтобы получить предыдущий рабочий день
     * @returns {Object}
     */
    getPrevDay() {
        let defaultDay = {date: this.moment(this.$props.value, 'DD.MM.YYYY').subtract(1, 'days'), addDays: 1};
        if (!this.$props.disabledDates || !this.$props.disabledDates.from) return defaultDay;
        let result = false;
        if (this.$props.disabledDates.dates) {
            let firstDay = this.moment(this.$props.disabledDates.to, 'DD.MM.YYYY');
            //составим массив выходных, для простого поиска
            let weekends = this.$props.disabledDates.dates.map(item => this.moment(item).format('DD.MM.YYYY')), prevDay;

            //идем на 2 недели назад и ищем ближайшую дату, когда работает компания
            for (let i = 1; i <= 14; i++) {
                if (result) return result; //если дата уже найдена, то вернем результат
                prevDay = this.moment(this.$props.value, 'DD.MM.YYYY').subtract(i, 'days');
                //если следующий день ДО минимальной даты и компания в этот день работает
                if (prevDay.isAfter(firstDay) && !(weekends.indexOf(prevDay.format('DD.MM.YYYY'))+1))
                    result = {date: prevDay, addDays: i};
            }
        }

        //если в итоге ничего не нашли, вернем стандарт - 1 день
        return defaultDay;
    },
    onPlus() {
      if (!this.canNextDay) return;
      let nextDate = this.getNextDay();
      this.inputValue = this.inputValue.addDays( nextDate.addDays );
    },
    onMinus() {
      if (!this.canPrevDay) return;
      let prevDate = this.getPrevDay();
      this.inputValue = this.inputValue.removeDays( prevDate.addDays );
    }
  },
  created() {
    this.$nextTick(() => {
      this.inputValue = this.$props.value;

      Date.prototype.addDays = function(days) {
        let date = new Date(this.valueOf());
        date.setDate(date.getDate() + days);
        return date;
      };

      Date.prototype.removeDays = function(days) {
        let date = new Date(this.valueOf());
        date.setDate(date.getDate() - days);
        return date;
      };

    });
  }
}



