import pageTitle from './../../../../components/page-title/index.vue';
import btn from './../../../../components/btn/index.vue';
import inputText from './../../../../components/input-text/index.vue';
import inputSelect from './../../../../components/input-select/index.vue';
import InputSuggestions from './../../../../components/input-suggestions/index.vue';
export default {
  components: {pageTitle, btn, inputText, inputSelect, InputSuggestions},
  data () {
    return {
        title: 'Добавление адреса',
        pageTitle: 'Укажите ваш новый адрес',
        form: {
            id: null,
            district: null,
            street: '',
            house: '',
            level: '',
            apartment: ''
        },
        formDefault: {
            id: null,
            district: null,
            street: '',
            house: '',
            level: '',
            apartment: ''
        }
    }
  },
  computed: {
    districts() {
      return this.$store.state.address.districts;
    },
    addresses() {
      return this.$store.state.address.addresses;
    },
    config() {
        return this.$root.config.screens.address;
    }
  },
  watch: {},
  methods: {
    init() {
      this.title     = this.config.strings.title.value;
      this.pageTitle = this.config.strings.pageTitle.value;

      this.$store.dispatch('address/getAddresses');
      this.$store.commit('setTitle', this.title);
      this.$store.commit('showBackButton');
      this.$store.commit('toggleToolbar', true);
      this.$store.commit('setBackRoute', {name: 'Addresses'});

      if (this.$route.params.id != 'new') this.getAddress();
      this.$root.addEnterListener((e) => this.save());
    },
    getAddressId() {
      //возвращает количество секунд
      this.form.id = this.moment().format('X');
      return this.form.id;
    },
    getAddress() {
       this.$store.dispatch('address/getAddress', this.$route.params.id).then(r => {
         this.form = Object.assign({}, r);
       });
    },
    save() {
      this.$validator.validateAll().then((valid) => {
        if (!valid) return;
        this.form.date = new Date();
        if (this.form.id) {
          this.$store.commit('address/changeAddress', Object.assign({}, this.form));
        } else {
          this.getAddressId();
          this.$store.commit('address/addAddress', Object.assign({}, this.form));
        }
        this.toOrder(this.form.id);
      });
    },
    toOrder(id) {
        //устанавливаем текущий адрес как выбранный и отправляем пользователя на форму заказа
        this.$store.dispatch('order/getLocalOrderByAddressId', id).then((order) => {
            if (order) {
                order.date = new Date();
                this.$store.commit('order/setNewOrder', order);
            } else this.$store.commit('order/setBlankOrder');

            this.$store.commit('order/setOrderAddress', this.form);
            this.$router.push({name: 'NewOrder'});
        });
    },


  },
  created: function() {
    this.init();
  },
}



