import pageTitle from './../../../../components/page-title/index.vue';
import btn from './../../../../components/btn/index.vue';
import inputText from './../../../../components/input-text/index.vue';
import inputTextarea from './../../../../components/input-textarea/index.vue';
import inputValue from './../../../../components/input-value/index.vue';
import popup from './../../../../components/popup/index.vue';

export default {
  components: {pageTitle, btn, inputText, inputValue, inputTextarea, popup},
  data () {
    return {
      title: 'Заказ воды',
      pageTitle: 'Проверьте ваш заказ',
      loading: false,
      form: {
        waterBig: {value: 1, price: 200},
        bottleBig: {value: 0, price: 450},
        pump: {value: 0, price: 650},
        waterSmall: {value: 0, price: 55},
        date: new Date(),
        time: 1,
        time_text: '',
        phone: '',
        comment: '',
        address: {}
      },
      showSuccessMessage: false,
      showErrorMessage: false,
    }
  },
  computed: {
    products() {
      let result = '<div><b>Вода 19л.</b><span>'    + this.form.waterBig.value   + ' шт.</span><span>' + this.form.waterBig.price   + ' руб.</span></div>';
      if (this.form.waterSmall.value)
          result += '<div><b>Вода 5л.</b><span>'     + this.form.waterSmall.value + ' шт.</span><span>' + this.form.waterSmall.price + ' руб.</span></div>';
      if (this.form.bottleBig.value)
          result += '<div><b>Ёмкость 19л.</b><span>' + this.form.bottleBig.value  + ' шт.</span><span>' + this.form.bottleBig.price  + ' руб.</span></div>';
      if (this.form.pump.value)
          result += '<div><b>Насос</b><span>'        + this.form.pump.value       + ' шт.</span><span>' + this.form.pump.price       + ' руб.</span></div>';

      return result;
    },
    config() {
      return this.$root.config.screens.checkOrder;
    },
    times() {
      return this.$store.state.order.times;
    },
    order() {
      return this.$store.state.order.new_order;
    },
    districts() {
      return this.$store.state.address.districts;
    },
    successOrderMessage() {
      let message = this.config.strings.text_send_order_success_message.value.split('%datetime%');
      message = message[0] + this.form.time_text + message[1];
      return message;
    },
    addressText() {
      return this.getDistrict(this.form.address.district) + ', ' +
             ' ул. ' + this.form.address.street + ', ' +
             ' дом ' + this.form.address.house + ', ' +
             ' этаж ' + this.form.address.level + ', ' +
             ' квартира/офис ' + this.form.address.apartment;
    },
    price() {
      return parseFloat(this.form.waterBig.price)
           + parseFloat(this.form.bottleBig.price)
           + parseFloat(this.form.waterSmall.price)
           + parseFloat(this.form.pump.price);
    }
  },
  watch: {
    'order'() {
      this.getOrder();
    },
    'form.phone'() {
        this.$store.commit('order/setNewOrder', this.form);
    },
    'form.comment'() {
        this.$store.commit('order/setNewOrder', this.form);
    }
  },
  methods: {
    init() {
      this.title = this.config.strings.title.value;
      this.pageTitle = this.config.strings.pageTitle.value;

      this.$store.commit('setTitle', this.title);
      this.$store.commit('showBackButton');
      this.$store.commit('toggleToolbar', true);
      this.$store.dispatch('order/getNewOrder');
      this.$store.commit('setBackRoute', {name: 'NewOrder'});

      this.getOrder();
      this.$root.addEnterListener((e) => this.save());
    },
    getDistrict(id) {
      let district = this.districts.find(item => parseInt(item.id) === parseInt(id));
      return (district) ? district.name : '';
    },
    getOrder() {
      if (this.order && JSON.stringify(this.order) !== JSON.stringify(this.form)) this.form = Object.assign({}, this.order);
    },
    save() {
        this.$validator.validateAll().then((valid) => {
          if (!valid) return;
          this.loading = true;


          //обновляем в адресе дату последнего заказа
          this.form.address.last_order = this.moment().format('X');
          this.$store.commit('address/changeAddress', this.form.address);
          //сохраняем заказ в сторе
          this.$store.commit('order/setNewOrder', this.form);
          //сохраняем заказ в LocalStorage
          this.form.removePumpAndBottle = false;
          this.$store.commit('order/saveOrderToLocal', this.form);

          //отправляем заказ на backend
          this.$store.dispatch('order/postOrder', this.form).then(r => {
            if (r) {
              this.showSuccessMessage = true;
                this.$root.removeEnterListeners()
            } else this.showErrorMessage = true;
            this.loading = false;
          });

      });
    },
    onSuccess(e) {
      this.showSuccessMessage = false;
      //сохраняем заказ в LocalStorage
      this.form.removePumpAndBottle = true;
      this.$store.commit('order/saveOrderToLocal', this.form);
      this.$router.push({name: 'Home'});
    },
    onError(e) {
      this.showErrorMessage = false;
    },
  },
  created: function() {
    this.init();
  },
}



