import pageTitle from './../../../../components/page-title/index.vue';
import btn from './../../../../components/btn/index.vue';
import inputText from './../../../../components/input-text/index.vue';
import inputSelect from './../../../../components/input-select/index.vue';
export default {
  components: {pageTitle, btn, inputText, inputSelect},
  data () {
    return {
      title: 'О компании',
      pageTitle: 'Серебряный родник',
    }
  },
  computed: {
    config() {
      return this.$root.config.screens.about;
    }
  },
  watch: {},
  methods: {
    init() {
      if (this.config.strings.title.value)     this.title     = this.config.strings.title.value;
      if (this.config.strings.pageTitle.value) this.pageTitle = this.config.strings.pageTitle.value;

      this.$store.dispatch('address/getAddresses');
      this.$store.commit('setTitle', this.title);
      this.$store.commit('showBackButton');
      this.$store.commit('toggleToolbar', true);
      this.$store.commit('setBackRoute', {name: 'Home'});


      this.$nextTick(() => {
        document.querySelectorAll('.tellink').forEach(item => {
          item.addEventListener('click', e => {
              e.preventDefault();
              window.open(e.target.getAttribute('href'), '_system');
          });
        });
      });

    }
  },
  created: function() {
    this.init();
  },
}



