import circleBtn from './../circle-btn/index.vue';
import popup from './../popup/index.vue';
export default {
  name: 'user-addresses',
  components: {circleBtn, popup},
  props: [],
  data() {
    return {
      showChangeDeleteDialog: false,
      showDeleteDialog: false,
      changeDeleteAddress: {},
      deleteAddress: {},
    }
  },
  watch: {
    '$router.name'() {
      this.getAddresses();
    }
  },
  computed: {
    addresses() {
      let addresses = this.$store.state.address.addresses;
      if (!addresses || !addresses.length) return [];

      //если у адреса ещё нет поля времени последнего заказа,
      //то сделаем его 30 дней назад (чтобы последние адреса были всегда выше)
      addresses = addresses.map(item => {
        if (!item.last_order) item.last_order = this.moment().subtract(30, 'days').format('X');
        return item;
      });

      return addresses.sort((a, b) => ((a.last_order === b.last_order) ? 0 : ((a.last_order > b.last_order) ? -1 : 1)));
    },
    districts() {
      return this.$store.state.address.districts;
    },
    config() {
      return this.$root.config.screens.addresses;
    }
  },
  methods: {
    getAddresses() {
      return this.$store.dispatch('address/getAddresses');
    },
    getDistrict(id) {
      let district = this.districts.find(item => parseInt(item.id) === parseInt(id));
      return (district) ? district.name : '';
    },
    onLongTouch(address) {
      this.changeDeleteAddress = Object.assign({}, address);
      this.showChangeDeleteDialog = true;
    },
    toOrder(address) {
      //получаем закз для этого адреса из LocalStorage и отправляем пользователя на форму заказа
      this.$store.dispatch('order/getLocalOrderByAddressId', address.id).then(order => {
        if (order) {
          order.date = new Date();
          this.$store.commit('order/setNewOrder', order);
        } else this.$store.commit('order/setBlankOrder');

        this.$store.commit('order/setOrderAddress', address);
        this.$router.push({name: 'NewOrder'});
      });
    },
    onHide() {
      this.changeDeleteAddress = {};
    },
    onChange(id) {
      if (!id) id = this.changeDeleteAddress.id;

      this.$router.push({name: 'Address', params: {id: id}});
      this.showChangeDeleteDialog = false;
      this.changeDeleteAddress = {};
    },
    onOneTouchDelete(address) {
        this.deleteAddress = Object.assign({}, address);
        this.changeDeleteAddress = {};
        this.showDeleteDialog = true;
        this.showChangeDeleteDialog = false;
    },
    onDelete() {
      this.deleteAddress = Object.assign({}, this.changeDeleteAddress);
      this.changeDeleteAddress = {};
      this.showDeleteDialog = true;
      this.showChangeDeleteDialog = false;
    },
    onHideDeleteDialog() {
      this.deleteAddress = {};
    },
    onConfirmDelete() {
      this.deleteAddress = {};
      this.showDeleteDialog = false;
    },
    onRejectDelete() {
      this.$store.commit('address/removeOrderAddress', this.deleteAddress.id);
      this.deleteAddress = {};
      this.showDeleteDialog = false;
    }
  },
  created() {
    this.getAddresses();
  },
}



