export default {
  name: 'icon-btn',
  props: [
    'to', 'addClass', 'type', 'disabled', 'size', 'icon'
  ],
  data() {
    return {}
  },
  created() {},
  watch: {},
  computed: {
    typeVal() {
      return (this.$props.type && (['primary', 'accend', 'success'].indexOf(this.$props.type)+1)) ? this.$props.type : 'primary';
    },
    sizeVal() {
      return (this.$props.size && (['small', 'normal', 'big', 'extra-big'].indexOf(this.$props.size)+1)) ? this.$props.size : 'normal'
    }
  },
  methods: {
    onClick(e) {
      e.preventDefault();
      if (this.$props.disabled) return;
      if (this.$props.to) this.$router.push(this.$props.to);
      this.$emit('click', e);
    }
  }
}



