export default {
  name: 'toolbar',
  props: ['title', 'showBackButton'],
  data() {
    return {}
  },
  created() {},
  watch: {},
  computed: {
    config() {
        return this.$root.config.screens.toolbar;
    },
    backRoute() {
      return this.$store.state.backRoute;
    },
    titleValue() {
      return (this.$props.title) ? this.$props.title : this.$store.state.title;
    },
    backButton() {
      return (this.$props.showBackButton) ? this.$props.showBackButton : this.$store.state.backButton;
    }
  },
  methods: {
    goBack() {
      if (this.backRoute) this.$router.push(this.backRoute);
      else this.$router.go(-1);
    }
  }
}



