exports.install = function (Vue, options) {
  if (!options) options = {};
  if (!options.duration) options.duration = 500;
  if (!options.onTouchStartClass) options.onTouchStartClass = 'touched';

  Vue.directive('long-touch', {
    bind: function (el, binding) {
      let self = this;
      self.timeout = null;

      this.touchstart = function (e) {
        let context = this;
        el.classList.add(options.onTouchStartClass);
        self.timeout = setTimeout(function () {
          binding.value.call(context, e);
          el.classList.remove(options.onTouchStartClass);
        }, options.duration)
      };

      this.cancelTouch = function (e) {
        clearTimeout(self.timeout);
        el.classList.remove(options.onTouchStartClass);
      };

      el.addEventListener("touchstart",  this.touchstart,  false);
      el.addEventListener("touchend",    this.cancelTouch,    false);
      el.addEventListener("touchcancel", this.cancelTouch, false);
      el.addEventListener("touchmove",   this.cancelTouch,   false);
    },
    unbind: function (el) {
      el.removeEventListener("touchstart",  this.touchstart,  false);
      el.removeEventListener("touchend",    this.touchend,    false);
      el.removeEventListener("touchcancel", this.touchcancel, false);
      el.removeEventListener("touchmove",   this.touchmove,   false);
    }
  })
};



