import btn from './../../../../components/btn/index.vue';
import popup from './../../../../components/popup/index.vue';
export default {
    name: 'home',
    components: {btn, popup},
    data () {
        return {
            title: 'Серебряный родник',
            showHelloMessage: false
        }
    },
    computed: {
        config() {
            return this.$root.config.screens.home;
        }
    },
    methods: {
        init() {
            this.title = this.config.strings.title.value;

            this.$store.commit('setTitle', this.title);
            this.$store.commit('hideBackButton');
            this.$store.commit('toggleToolbar', false);
            this.$store.commit('setBackRoute', {name: 'Home'});
            this.$nextTick(() => setTimeout(() => this.helloMessage(), 1000));
        },
        onSuccess() {
            this.showHelloMessage = false;
        },
        helloMessage() {
            let firstStart = localStorage.getItem('ss_first_start');
            if (!firstStart || parseInt(firstStart) === 0) {
                this.showHelloMessage = true;
                localStorage.setItem('ss_first_start', 1);
            }
        }
    },
    created:  function() {
        this.init();
    },
}


