<template>
  <div id="application" ref="application">
    <snackbar></snackbar>
    <online></online>
    <transition name="slidetoolbar" mode="out-in">
      <toolbar v-show="showToolbar" :class="{hide: !showToolbar, 'toolbar-container': true}" ref="toolbar"></toolbar>
    </transition>

    <div v-bind:class="'page' + ((showToolbar) ? ' with-toolbar' : '')" ref="page">
      <transition name="slideview" mode="out-in">
        <router-view class="view"></router-view>
      </transition>
    </div>
  </div>
</template>
<script>
  import toolbar from './components/toolbar/index.vue';
  import snackbar from './components/snackbar/index.vue';
  import online from './components/online/index.vue';
  export default {
    name: 'app',
    components: {toolbar, snackbar, online},
    data () {
      return {}
    },
    watch: {
        '$route'() {
            this.$nextTick(() => this.calcPageHeight());
        },
        'showToolbar'() {
            this.$nextTick(() => this.calcPageHeight());
        }
    },
    computed: {
      showToolbar() {
        return this.$store.state.toolbar;
      }
    },
    methods: {
        calcPageHeight() {
            setTimeout(() => {
              let size = document.querySelector('body').getBoundingClientRect();
              this.$refs.application.style.height = size.height + 'px';

              if (!this.showToolbar) {
                  this.$refs.page.style.height = size.height + 'px';
              } else {
                  let toolbarSize = this.$refs.toolbar.$el.getBoundingClientRect();
                  this.$refs.page.style.height = (size.height - toolbarSize.height) + 'px';
              }
            },0);
        }
    },
    created() {
        this.$nextTick(() => this.calcPageHeight());
    }
  }
</script>
<style>
  /*
  #component-toolbar {
    transition: 0s;
  }
  #component-toolbar.hide{
    opacity: 0;
  }
  */
  body{
    background-color: var(--base-bg-color);
  }
  #application{
    width: 100%;
    height: 100vh;
    overflow: hidden;
  }
  #application .page{
    height: 100vh;
    width: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: var(--base-bg-color);
    font-size: 14px;
    box-sizing: border-box;
  }
  #application .page.with-toolbar{height: calc(100vh - 40px); margin-top: 40px;}
  #application .view{min-height: calc(100% - 20px); padding: 10px; position: relative;}

  /** Change view animation **/
    .slideview-leave-active{transition: opacity 0.2s;}
    .slideview-leave-to{opacity: 0;}

    .slideview-enter-active{transition: opacity 1s, transform 0.5s;}
    .slideview-enter{
      transform: perspective(1000px) translateY(30%) scale(1.2);
      opacity: 0;
      z-index: 999;
      background-color: var(--base-bg-color);
    }


    .slidetoolbar-enter-active{transition: opacity 0.5s, transform 0.5s;}
    .slidetoolbar-leave-active{transition: opacity 0.5s, transform 0.5s;}
    .slidetoolbar-enter,
    .slidetoolbar-leave-to{transform: translateY(-30%); opacity: 0;}
  /** **/




  @media (min-width: 400px) {
    #application .page.with-toolbar{height: calc(100vh - 50px); margin-top: 50px;}
  }
  @media (min-width: 350px) {
    #application .page.with-toolbar{height: calc(100vh - 50px); margin-top: 50px;}
  }
  @media (min-width: 320px) {}
</style>



