import Addresses from './views/addresses/index.vue'
import Address from './views/address/index.vue'

export default {
  routes: [
    {
      path: '/addresses',
      name: 'Addresses',
      component: Addresses
    },
    {
      path: '/addresses/:id',
      name: 'Address',
      component: Address
    }
  ]
}



