import NewOrder from './views/newOrder/index.vue';
import CheckOrder from './views/checkOrder/index.vue';

export default {
  routes: [
    {
      path: '/order/new',
      name: 'NewOrder',
      component: NewOrder
    },
    {
      path: '/order/new/check',
      name: 'CheckOrder',
      component: CheckOrder
    }
  ]
}



