import btn from './../btn/index.vue';
import iconBtn from './../icon-btn/index.vue';
import inputText from './../input-text/index.vue';
export default {
  name: 'count-selector',
  components: {btn, iconBtn, inputText},
  props: {
    label:  {type: String, default: ''},
    labelInside:  {type: Boolean, default: true},
    name: {type: String, default: ''},
    min: {type: Number, default: 0},
    max: {type: Number, default: 999},
    price: {type: Number, default: 0},
    error: {type: String, default: ''},
    size: {type: String, default: 'normal'},
    value: {
      type: Object,
      default: () => {
        return {value: 0, price: 0};
      }
    },
  },
  data() {
    return {
      inputValue: {value: 0, price: 0},
      textValue: ''
    }
  },
  watch: {
    '$props.value.value'(newVal) {
      if (parseInt(this.inputValue.value) != parseInt(newVal)) {
        this.inputValue.value = newVal;
        this.calc();
      }
    },
    'inputValue.value'(newVal) {
      let resultValue = {value: newVal, price: (parseInt(this.inputValue.value) * parseFloat(this.$props.price))};
      this.$emit('input', resultValue);
      this.$emit('change', resultValue);
    }
  },
  computed: {
    inputId() {
      return 'input-' + this._uid;
    },
    sizeVal() {
      return (this.$props.size && (['small', 'normal', 'big', 'extra-big'].indexOf(this.$props.size)+1)) ? this.$props.size : 'normal'
    }
  },
  methods: {
    onFocus() {
      this.$emit('focus');
    },
    onBlur() {
      this.$emit('blur');
    },
    onPlus() {
      let newVal = (parseInt(this.inputValue.value) + 1);
      if (newVal > parseInt(this.$props.max)) return;
      this.inputValue.value = newVal;
      this.calc();
    },
    onMinus() {
      let newVal = (parseInt(this.inputValue.value) - 1);
      if (newVal < parseInt(this.$props.min)) return;
      this.inputValue.value = newVal;
      this.calc();
    },
    calc() {
      this.inputValue.price = (parseInt(this.inputValue.value) * parseFloat(this.$props.price));
      if (this.$props.labelInside) this.textValue = '<b>' + this.$props.label + ': </b>';
      this.textValue += '<span>' + this.inputValue.value + ' шт.<b>/ </b>' + this.inputValue.price + ' руб.</span>'
    }
  },
  created() {
    this.$nextTick(() => {
      this.inputValue = Object.assign({}, this.$props.value);
      this.calc();
    });
  }
}



