import popup from './../popup/index.vue';
export default {
    name: 'online',
    props: {},
    components: {popup},
    data () {
        return {
            status: true,
            showErrorMessage: false
        }
    },
    computed: {
        config() {
            return this.$root.config.screens.offline;
        },
    },
    watch: {},
    methods: {
        change() {
            let result  = this.status = (typeof window.navigator.onLine === 'undefined') ? true : window.navigator.onLine;

            if (!result) this.showErrorMessage = true;
            else this.showErrorMessage = false;

            return result;
        },
        onHide() {
            this.$nextTick(() => {
                this.showErrorMessage = true;
            });
        },
        onSuccess() {
            this.$nextTick(() => {
                this.showErrorMessage = true;
            });
            this.$root.exitApp();
        }
    },
    created: function() {
        this.$nextTick(() => {
            window.addEventListener('online',  () => this.change());
            window.addEventListener('offline', () => this.change());
        });
    }
}


