import btn from './../btn/index.vue';
export default {
  name: 'popup',
  components: {btn},
  props: {
    show: {type: Boolean, default: false},
    message: {type: String, default: ''},
    cancelText: {type: String, default: 'Отмена'},
    successText: {type: String, default: 'Ок'},
    hideCancel: {type: Boolean, default: false},
    hideSuccess: {type: Boolean, default: false}
  },
  data() {
    return {}
  },
  watch: {
    '$props.show'(val) {
        this.$store.commit('popup', !!(val));
    }
  },
  computed: {},
  methods: {
    onSuccess(e) {
      this.$emit('success', e);
      this.$store.commit('popup', false);
    },
    onCancel(e) {
      this.$emit('cancel', e);
      this.$store.commit('popup', false);
    },
    hide(e) {
      this.$emit('hide', e);
      this.$emit('update:show', false);
      this.$store.commit('popup', false);
    }
  },
  created() {
      this.$root.$on('close-popup', () => {
          this.$emit('update:show', false);
      });
  }
}



