import Vue from 'vue';
import App from './App.vue';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import { sync } from 'vuex-router-sync';
import { store } from './store';
import { routes as ModuleRoutes, stores as ModuleStores} from './views';
import VeeValidate from 'vee-validate';
import ValidationMessages from 'vee-validate/dist/locale/ru';
import Moment from 'moment';
import LongTouch from './directives/longTouch';
import config from './config.js';

/** Font Awesome **/
import { library } from '@fortawesome/fontawesome-svg-core'
//import {faThumbsDown as RFaThumbsDown} from '@fortawesome/free-regular-svg-icons';
import {faPlus, faMinus, faArrowLeft, faArrowRight, faTrash, faEdit} from '@fortawesome/free-solid-svg-icons'
library.add([faPlus, faMinus, faArrowLeft, faArrowRight, faTrash, faEdit]);
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
Vue.component('font-awesome-icon', FontAwesomeIcon);

import './assets/css/normalize.css';
import './assets/fonts/roboto.css';
import './assets/css/main.css';

Moment.locale('ru');
Vue.prototype.moment = Moment;
Vue.prototype.config = config;
Vue.use(VueRouter);
Vue.use(VueResource);
Vue.use(VeeValidate, {locale: 'ru', events: 'blur', dictionary: {ru: ValidationMessages}});
Vue.use(LongTouch, {duration: 500});

Vue.http.headers.common['Authorization'] = localStorage.getItem('ss_token');
Vue.http.headers.common['Accept'] = 'application/json, text/plain, */*';
Vue.http.headers.common['X-Requested-With'] = "XMLHttpRequest";
Vue.http.headers.common['Access-Control-Allow-Origin'] = '*';
Vue.http.options.emulateHTTP = true;

let router = new VueRouter({
  mode: 'hash',
  assetsPublicPath: '',
  scrollBehavior: () => ({ y: 0 }),
  linkActiveClass: 'router-link-active active',
  routes: [].concat(ModuleRoutes)
});
const unsync = sync(store, router);

new Vue({
  store,
  router,
  el: '#app',
  data() {
    return {
      exit: false,
      enterListeners: []
    }
  },
  methods: {
    exitApp() {
      if (navigator.app) navigator.app.exitApp();
      else if (navigator.device) navigator.device.exitApp();
      else window.close();
    },
    backListener() {
        document.addEventListener('backbutton', e => {
            e.preventDefault();

            //если в момент нажатия открыто модальное окно, то закроем его
            if (this.popup) {
                this.$root.$emit('close-popup');
                return; //и никуда не возвращаем пользователя
            }

            if (this.backRoute && this.$route.name !== 'Home') this.$router.push(this.backRoute);
            else {
              if (this.exit) this.exitApp();
              this.$store.commit('showSnackbar', ["Для выхода нажмите 'Назад' ещё раз", 2000]);
              this.exit = true;
              setTimeout(() => {this.exit = false;}, 2000);
            }
        }, false);
    },
    noSelect(node) {
        if (node.nodeType == 1) node.classList.add('no-select');
        let child = node.firstChild;
        while (child) {
            this.noSelect(child);
            child = child.nextSibling;
        }
    },
    offMobileAccessibility() {
      //отключаем масштабирование, если оно включено в настройках телефона
      if (window.MobileAccessibility) window.MobileAccessibility.usePreferredTextZoom(false);
      //if (MobileAccessibility) MobileAccessibility.usePreferredTextZoom(false);
    },
    addEnterListener(func) {
        this.enterListeners.push(func);
    },
    removeEnterListeners() {
        this.enterListeners = [];
    },
    enterPress() {
        document.addEventListener("keydown", (e) => {
            let btn = e.which || e.keyCode;
            if (btn === 13) {
                window.event.preventDefault();
                window.event.stopPropagation();
                e.preventDefault();
                e.stopPropagation();
                this.enterListeners.map(item => item(e));
            }
        });
    }
  },
  computed: {
    backRoute() {
        return this.$store.state.backRoute;
    },
    popup() {
        return this.$store.state.popup;
    }
  },
  watch: {
    '$route'() {
        this.noSelect(document.getElementById('application'));
        this.offMobileAccessibility();
        this.enterListeners = [];
    }
  },
  created: function () {
    document.addEventListener('DOMContentLoaded', () => {
      this.$nextTick(() => {
        this.offMobileAccessibility();
        this.$store.dispatch('init');
        this.noSelect(document.getElementById('application'));
        this.backListener();
        this.enterPress();
      });
    });
  },
  render: h => h(App)
});



