import Vue from 'vue'
export default {
    store: {
        namespaced: true,
        state: {
            endpoint: 'address',
            address: {},
            addresses: [],
            schedule: [],
            scheduleByArea: {},
            notDefaultScheduleByArea: {},
            defaultDistrict: {id: null, name: 'Укажите ваш район'},
            districts: [
                {id: null, name: 'Укажите ваш район', price_big: 200.0, price_small: 55.0, price_bottle: 450, price_pump: 650},
                {id: 1, name: 'Южно-Сахалинск', price_big: 150.0, price_small: 55.0, price_bottle: 450, price_pump: 650},
                {id: 2, name: 'Октябрьское', price_big: 150.0, price_small: 55.0, price_bottle: 450, price_pump: 650},
                {id: 3, name: 'Дальнее', price_big: 200.0, price_small: 55.0, price_bottle: 450, price_pump: 650},
                {id: 4, name: 'Луговое/Ново-Александровка', price_big: 200.0, price_small: 55.0, price_bottle: 450, price_pump: 650}
            ]
        },
        mutations: {
            setAddresses(state, addresses) {
                state.addresses = Object.assign([], addresses);
                localStorage.setItem('ss_user_addresses', JSON.stringify(addresses));
            },
            addAddress(state, address) {
                let addresses = Object.assign([], state.addresses);
                addresses.push(address);
                this.commit('address/setAddresses', addresses);
            },
            changeAddress(state, address) {
                this.dispatch('address/getAddresses').then(addresses => {
                  addresses = addresses.map(item => {
                    if (parseInt(item.id) === parseInt(address.id)) item = Object.assign({}, address);
                    return item;
                  });
                  this.commit('address/setAddresses', addresses);
                });
            },
            removeOrderAddress(state, id) {
                return this.dispatch('address/getAddresses').then(addresses => {
                  addresses = addresses.filter(item => !(parseInt(item.id) === parseInt(id)));
                  this.commit('address/setAddresses', addresses);
                  this.commit('order/removeLocalOrderByAddressId', id);
                  return addresses;
                });
            }
        },
        actions: {
            getAddresses({ state, dispatch, rootState }) {
                let addresses = localStorage.getItem('ss_user_addresses');
                addresses = (addresses) ? JSON.parse(addresses) : [];
                Vue.set(state, 'addresses', addresses);
                return addresses;
            },
            getAddress({ state, dispatch, rootState }, id) {
                return this.dispatch('address/getAddresses').then(addresses => {
                  let address = addresses.find(item => parseInt(item.id) === parseInt(id));
                  Vue.set(state, 'address', address);
                  return address;
                });
            },
            getDistricts({ state, dispatch, rootState }) {
                return Vue.http.get(rootState.endpoint + '/area').then(
                    response => {
                        let result = [state.defaultDistrict];
                        if (response.body.response && response.body.response.length)
                        response.body.response.map(item => result.push(item));

                        Vue.set(state, 'districts', result);
                        return result;
                    }, response => {
                        this.commit('showSnackbar', [response.body.errors.messages, 6000]);
                    }
                );
            },
            getSchedules({ state, dispatch, rootState }) {
                return Vue.http.get(rootState.endpoint + '/schedule', {params: {filter: [['type', '=', '1']]}}).then(
                    response => {
                        let schedule = response.body.response, scheduleByArea = {};
                        Vue.set(state, 'schedule', schedule);

                        schedule.map(item => {
                            let key = item.area_id;
                            if (!scheduleByArea[key]) scheduleByArea[key] = [];
                            scheduleByArea[key].push(item);
                        });
                        Vue.set(state, 'scheduleByArea', scheduleByArea);
                        return schedule;
                    }, response => {
                        this.commit('showSnackbar', [response.body.errors.messages, 6000]);
                    }
                );
            },
            getNotDefaultSchedules({ state, dispatch, rootState }) {
                return Vue.http.get(rootState.endpoint + '/schedule', {params: {filter: [['type', '=', '0']]}}).then(
                    response => {
                        let schedule = response.body.response, scheduleByArea = {};

                        schedule.map(item => {
                            let key = item.area_id;
                            if (!scheduleByArea[key]) scheduleByArea[key] = [];
                            scheduleByArea[key].push(item);
                        });
                        Vue.set(state, 'notDefaultScheduleByArea', scheduleByArea);
                        return schedule;
                    }, response => {
                        this.commit('showSnackbar', [response.body.errors.messages, 6000]);
                    }
                );
            },
            getSuggest({ state, dispatch, rootState }, data) {
                return Vue.http.get(rootState.endpoint + '/suggest', {params: data}).then(
                    response => response.body.response,
                    response => {
                        this.commit('showSnackbar', [response.body.errors.messages, 6000]);
                        return false;
                    }
                );
            },
        }
    }
}



