import VueSuggestions from 'vue-suggestions';
export default {
  name: 'input-suggestions',
  props: {
    label:  {type: String, default: ''},
    showLabel:  {type: Boolean, default: true},
    placeholder: {type: String, default: ''},
    type: {type: String, default: 'text'},
    value: {type: [String, Number, Date], default: () => ''},
    size: {type: Number, default: 100},
    name: {type: String, default: ''},
    error: {type: String, default: ''},
    disabled: {type: Boolean, default: false},
    readonly: {type: Boolean, default: false},
    locations: {type: Array, default: () => [{"region":"Сахалинская","city":"Южно-Сахалинск"}]}
  },
  data() {
    return {
      inputValue: '',
      suggestions: [],
      suggestLoad: false
    }
  },
  created() {
    this.$nextTick(() => {
      this.inputValue = this.$props.value;
    });
  },
  watch: {
    '$props.value'(newVal) {
      this.inputValue = newVal;
    },
    'inputValue'(newVal) {
      this.$emit('input', newVal);
      this.$emit('change', newVal);
    }
  },
  computed: {
    inputId() {
      return 'input-' + this._uid;
    },
    inputSize() {
      return (([100, 50, 33].indexOf(this.$props.size)+1)) ? this.$props.size : 100;
    }
  },
  methods: {
    onFocus() {
      this.$emit('focus');
      this.suggest();
    },
    onBlur() {
      this.$emit('blur');
      this.suggestions = [];
    },
    suggest() {
        if (this.inputValue && this.inputValue.length > 2) this.getSuggest(this.inputValue);
        else this.suggestions = [];
    },
    onSelect(text) {
        this.suggestions = [];
        this.inputValue = text;
    },
    getSuggest(search) {
      if (this.suggestLoad) return;
      this.suggestLoad = true;

      this.$store.dispatch('address/getSuggest', {
        type: 'address', count: 3, query: search,
        locations: this.$props.locations
      }).then(r => {
        this.suggestLoad = false;
        if (r.suggestions && r.suggestions.length) this.suggestions = Object.assign([], r.suggestions.map(item => {
            return {label: item.value, value: item.data.street}
        }));
      });
    }
  }
}



