import pageTitle from './../../../../components/page-title/index.vue';
import btn from './../../../../components/btn/index.vue';
import userAddresses from '../../../../components/userAddresses/index.vue';
export default {
  components: {pageTitle, btn, userAddresses},
  data () {
    return {
      title: 'Выбор адреса',
      pageTitle: 'Выберете адрес для доставки или добавьте новый'
    }
  },
  computed: {
    config() {
      return this.$root.config.screens.addresses;
    }
  },
  methods: {
    init() {
      this.title     = this.config.strings.title.value;
      this.pageTitle = this.config.strings.pageTitle.value;

      this.$store.commit('setTitle', this.title);
      this.$store.commit('showBackButton');
      this.$store.commit('toggleToolbar', true);
      this.$store.commit('setBackRoute', {name: 'Home'});
    }
  },
  created:  function() {
    this.init();
  },
}



