import About from './views/about/index.vue'
import Callback from './views/callback/index.vue';

export default {
  routes: [
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/callback',
      name: 'Callback',
      component: Callback
    }
  ]
}



