import Vue from 'vue'
export default {
  store: {
    namespaced: true,
    state: {
      endpoint: 'order',
      days: [
        'sunday',    //воскресенье
        'monday',    //понедельник
        'tuesday',   //вторник
        'wednesday', //среда
        'thursday',  //четверг
        'friday',    //пятница
        'saturday'  //суббота
      ],
      times: [
        {id: 1, text: '09:00 - 13:00'},
        {id: 2, text: '17:00 - 20:00'}
      ],
      new_order: {
        waterBig: {value: 1, price: 0},
        bottleBig: {value: 0, price: 0},
        pump: {value: 0, price: 0},
        waterSmall: {value: 0, price: 0},
        date: null,
        time: 1,
        time_text: '',
        phone: '',
        comment: '',
        address: {}
      },
      blank_order: {
          waterBig: {value: 1, price: 0},
          bottleBig: {value: 0, price: 0},
          pump: {value: 0, price: 0},
          waterSmall: {value: 0, price: 0},
          date: null,
          time: 1,
          time_text: '',
          phone: '',
          comment: '',
          address: {}
      }
    },
    mutations: {
      setNewOrder(state, order) {
        state.new_order = Object.assign({}, order);
        localStorage.setItem('ss_new_order', JSON.stringify(state.new_order));
      },
      setBlankOrder(state) {
          state.new_order = Object.assign({}, state.blank_order);
          localStorage.setItem('ss_new_order', JSON.stringify(state.new_order));
      },
      setOrderAddress(state, address) {
        state.new_order.address = Object.assign({}, address);
        localStorage.setItem('ss_new_order', JSON.stringify(state.new_order));
      },
      saveOrderToLocal(state, order) {
        let newLocalOrder = order || state.new_order, localOrders;
        this.dispatch('order/getLocalOrders').then(localOrders => {
          if (!newLocalOrder.address || !newLocalOrder.address.id) return;
          let AId = newLocalOrder.address.id;

          if (order.removePumpAndBottle) {
              newLocalOrder.bottleBig = {value: 0, price: 0};
              newLocalOrder.pump = {value: 0, price: 0};
          }

          localOrders[AId] = Object.assign({}, newLocalOrder);
          localStorage.setItem('ss_orders', JSON.stringify(localOrders));
        });
      },
      removeLocalOrderByAddressId(state, addressId) {
          this.dispatch('order/getLocalOrders').then(localOrders => {
              if (!localOrders || !Object.keys(localOrders).length) return;
              delete localOrders[addressId];
              localStorage.setItem('ss_orders', JSON.stringify(localOrders));
          });
      }
    },
    actions: {
      getLocalOrders({ state, dispatch, rootState }) {
          let localOrders = localStorage.getItem('ss_orders');
          if (!localOrders || !localOrders.length) localOrders = {};
          else localOrders = JSON.parse(localOrders);

          return localOrders;
      },
      getLocalOrderByAddressId({ state, dispatch, rootState }, id) {
          return dispatch('getLocalOrders').then(localOrder => {
              if (localOrder[id]) return localOrder[id];
              else return null;
          });
      },
      getNewOrder({ state, dispatch, rootState }) {
        let newOrder = localStorage.getItem('ss_new_order');
        newOrder = (newOrder) ? JSON.parse(newOrder) : {
          waterBig: {value: 1, price: 200},
          bottleBig: {value: 0, price: 450},
          pump: {value: 0, price: 650},
          waterSmall: {value: 0, price: 55},
          date: new Date(),
          time: 1,
          time_text: '',
          phone: '',
          comment: '',
          address: {}
        };

        if (typeof newOrder.date === 'string') newOrder.date = new Date(newOrder.date);
        state.new_order = Object.assign({}, newOrder);
        Vue.set(state, 'new_order', Object.assign({}, newOrder));

        return newOrder;
      },
      postOrder({ state, dispatch, rootState }, data) {
        return Vue.http.post(rootState.endpoint + '/order', data).then(
            r  => r.body.response, () => false
        );
      },
      postCallback({ state, dispatch, rootState }, data) {
          return Vue.http.post(rootState.endpoint + '/callback', data).then(
              r  => r.body.response, () => false
          );
      }
    }
  }
}



