import pageTitle from './../../../../components/page-title/index.vue';
import btn from './../../../../components/btn/index.vue';
import inputText from './../../../../components/input-text/index.vue';
import inputTextarea from './../../../../components/input-textarea/index.vue';
import popup from './../../../../components/popup/index.vue';
export default {
  components: {pageTitle, btn, inputText, inputTextarea, popup},
  data () {
    return {
      title: 'Обратная связь',
      pageTitle: 'Оставьте отзыв или задайте свой вопрос',
      loading: false,
      form: {
        phone: '',
        comment: ''
      },
      showSuccessMessage: false,
      showErrorMessage: false
    }
  },
  computed: {
    config() {
      return this.$root.config.screens.callback;
    }
  },
  watch: {},
  methods: {
    init() {
      if (this.config.strings.title.value)     this.title     = this.config.strings.title.value;
      if (this.config.strings.pageTitle.value) this.pageTitle = this.config.strings.pageTitle.value;

      this.$store.dispatch('address/getAddresses');
      this.$store.commit('setTitle', this.title);
      this.$store.commit('showBackButton');
      this.$store.commit('toggleToolbar', true);
      this.$store.commit('setBackRoute', {name: 'About'});

      document.addEventListener("keyup", (e) => {
        e.preventDefault();
        if (e.keyCode === 13) this.save();
      });
    },
    save() {
      this.$validator.validateAll().then((valid) => {
        if (!valid) return;
        this.loading = true;

        this.$store.dispatch('order/postCallback', this.form).then(r => {
          if (r) this.showSuccessMessage = true;
          else this.showErrorMessage = true;
          this.loading = false;
        });
      });
    },
    onSuccess(e) {
      this.showSuccessMessage = false;
      this.form = {form: {phone: '', comment: ''}};
    },
    onError(e) {
      this.showErrorMessage = false;
    },
  },
  created: function() {
    this.init();
  },
}



